<template>
  <div
    v-if="isContactInfoStep && addressField"
    class="address-field__note"
  >
    <span
      v-text="charsRemaining"
    />&nbsp;<span
      v-text="translations.addressMaxCharacterSuffix"
    />
  </div>
  <span v-else />
</template>

<script>

 /**
 * Vue: Checkout address field (checkout-address-field)
 * -----------------------------------------------------------------------------
 *
 */

export default {
  name: 'CheckoutAddressField',
  props: {
    maxChars: {
      type: Number,
      default: 35,
    },
  },
  data() {
    const { checkout } = window.cnvs.strings
    return {
      addressField: null,
      addressFieldId: 'checkout_shipping_address_address1',
      addressLength: 0,
      translations: {
        ...checkout,
      },
    }
  },
  computed: {
    charsRemaining() {
      const remainder = this.maxChars - this.addressLength
      return remainder < 0 ? 0 : remainder
    },
    isContactInfoStep() {
      return Shopify.Checkout.step === 'contact_information'
    },
  },
  watch: {
    addressField(newAddressField) {
      if (newAddressField) {
        if (this.isContactInfoStep) {
          this.addressField.setAttribute('maxlength', this.maxChars)
        }
      }
    },
  },
  mounted() {
    if (this.isContactInfoStep) {
      this.setAddressField()
      this.limitFieldLength()
    }
  },
  methods: {
    limitFieldLength() {
      if (this.addressField.value.length > this.maxChars) {
        this.addressField.value = this.addressField.value.substr(0, this.maxChars)
      }
      this.addressLength = this.addressField.value.length
    },
    setAddressField() {
      this.addressField = document.getElementById(this.addressFieldId)
      this.addressField.setAttribute('maxlength', this.maxChars)
      this.addressField.addEventListener('keyup', this.limitFieldLength)
      this.addressField.addEventListener('change', this.limitFieldLength)
    },
  },
}
</script>